import React from "react"
import styled from "styled-components"
import { rhythm } from "../utils/typography"
import { MdEmail } from "react-icons/md"
import { FaFacebookSquare } from "react-icons/fa"
import { DiGithubBadge } from "react-icons/di"
import { graphql,  useStaticQuery } from "gatsby"

const FooterStyled = styled.footer`
  color: #EEE8A9;
  position: relative;
  height: auto;
  min-height:200px;
 background-image: linear-gradient(to right bottom, #009ed6, #009ae3, #0094ee, #008ef8, #0085ff);


.skewSVG {
  // position: absolute;
  // top: 0;
  width: 100%;
  height: 10vh;
  /* set height to pixels if you want angle to change with screen width */
}
  .footer-content{
    // position:relative;
    // margin-top:10vh;
     align-items: center;
  display: flex;
  flex-direction:column;
            margin-left: auto;
        margin-right: auto;
        max-width: ${rhythm(24)};
        padding: 0 ${rhythm(3 / 4)};
     
}
.horizontal-divider {
  border-top: 2px solid #EEE8A9;
  width:70%;
}

.link-container{
display:flex;
flex-direction:row;
width:50%;
justify-content:space-around;   
}
.link-container>a{
box-shadow:none
}
.link-container>a>svg{
 color: #EEE8A9;
  height:2.3em;
  width:auto;
  &:hover{
    transform:scale(1.2)
  }
}

`

const Footer = () => {
  const data = useStaticQuery(graphql`
      query SocialLinksQuery {
          site {
              siteMetadata {
                  social {
                      facebook
                      github
                      email
                      twitter
                  },
              }
          }
      }
  `)

  const { email,facebook,github } = data.site.siteMetadata.social;
  return (
    <FooterStyled>
      <svg className="skewSVG" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="none">
        <polygon fill="white" points="0,100 100,0 0,0"/>
      </svg>
      <div className="footer-content">
        <p className=" flex-item link-container ">
          <a href={"mailto:"+email} aria-label="Email">
            <MdEmail/>
          </a>
          <a href={"https://github.com/"+github} aria-label="Github">
            <DiGithubBadge/>
          </a>
          <a href={"https://facebook.com/"+facebook} aria-label="Facebook">

            <FaFacebookSquare/>
          </a>


         </p>
        <p className="flex-item horizontal-divider"></p>
        <p className="flex-item">
          © 2019 - {new Date().getFullYear()}</p>
      </div>
    </FooterStyled>
  )
}

export default Footer