import React from "react"
import styled from "styled-components"
import { rhythm } from "../utils/typography"
import { DiReact } from "react-icons/di";
import { graphql, Link, useStaticQuery } from "gatsby"


const HeaderStyled = styled.header`
    position: relative;
    height: 300px;
    background-image: linear-gradient(to right bottom, #009ed6, #009ae3, #0094ee, #008ef8, #0085ff);
 
    h1 {
        padding: 50px 0;
        margin:0 auto;
        text-align: center;
        color:#EEE8A9;
    }

    .skewSVG {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 10vh;
      }
      
    .header-content{
        margin-left: auto;
        margin-right: auto;
        max-width: ${rhythm(24)};
        padding: 0 ${rhythm(3 / 4)};
    }
    
    .blog-logo{
        height:2em;
        width:auto;
    }
`

const Header = () => {
  const data = useStaticQuery(graphql`
      query SiteTitleQuery {
          site {
              siteMetadata {
                  title
              }
          }
      }
  `)

  const { title } = data.site.siteMetadata
  return (
    <HeaderStyled>
      <div className="header-content">
        <Link to={"/"}>
          <h1><DiReact className='blog-logo' /><br/>{title}</h1>
        </Link>
      </div>
      <svg className="skewSVG" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="none">
        <polygon fill="white" points="0,100 100,0 100,100"/>
      </svg>
    </HeaderStyled>
  )
}

export default Header