import React from "react"
import styled from "styled-components"
import { rhythm } from "../utils/typography"

const Overlay = styled.div`
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,.3);
    z-index: 10;
    opacity: 0;
    visibility: hidden;
    transition: all 200ms ease-in;
    &.show-nav  {
      opacity: 1;
      visibility: visible;
    }
`

const Sidebar = styled.div`
  // max-height: inherit;
  // overflow:auto;

  width: 270px;
  height: 100vh;
  background-image: linear-gradient(to right bottom, #009ed6, #009ae3, #0094ee, #008ef8, #0085ff);
  right: -270px;
  top: 0;
  position: fixed;
  padding: 20px 0;
  box-shadow: 0 2px 4px rgba(0,0,0,.6);
  transition: all 200ms ease-in-out;
  z-index: 99;
  @media (max-width: 600px) {
   right:-100%;
   width:100%
  }

.toggle {
  position: absolute;
  right: 100%;
  background-color: #0085ff;
  width: 45px;
  height: 40px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;

  cursor: default;
  &::before{
   content:" ";
   transform-origin: bottom left;
  width: 100%;
    height: 100%;
    transform: skew(20deg, 0deg);
    background-color: #0085ff;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}
.nav-content-wrapper{
  max-height:90vh;
  overflow:auto;
}
.toggle-reverse {
  @media (min-width: 600px) {
   display:none
  }
    &::after{
   content:" ";
   transform-origin: bottom left;
  width: 100%;
    height: 100%;
    transform: skew(-20deg, 0deg);
  background:white;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  right: 100%;
  color:#0085ff;
  background-color: white;
  width: 45px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  cursor: default;
}

.toggle span,.toggle-reverse span {
  display: block;
  margin-top: -5px;
}

.toggle span {
  display: block;
  margin-right: 15px;
}


.toggle-reverse span {
margin-left:15px}
.logo {
padding: 0 ${rhythm(3 / 4)};
  text-align: center;
  margin-bottom: 30px;
}
.logo  a{
  text-decoration: none;
  color: #EEE8A9;
  font-size: 1.6rem;
  box-shadow:none;
}
& ul{
  margin:0;
  padding: 0 ${rhythm(3 / 4)};
  text-align:center;
  

}

& ul li {

  display: block;
  list-style: none;
}
& ul li a { 
position:relative;  
  box-shadow:none;
  padding: 10px 20px;
  display: block;
  color: #EEE8A9;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.3rem;
  font-weight:600;
  text-decoration: none;
  transition: all 200ms ease;
 
   
    &:after
   {
      content: '';
      display: block;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #EEE8A9;
      transition: all 0.3s ease;
      transform: scale(0.25);}


    &:hover:after{
      transform: scale(0.8);}
}


/* Show Nav */
&.show-nav {
  right: 0;
}
`

class SidebarMenu extends React.Component {
  state = {
    open: false
  }

  openStateToggler = () => {
    console.log("CLICK")
    this.setState(prevState => {
      return { open: !prevState.open }
    })
  }

  drawMenuIcon = ()=>{
    const {open} = this.state;
    return open?"»":"«";
  }

  render() {
    const {open} = this.state;
    return <React.Fragment>
      <Overlay className={open?" show-nav":""}/>
    <Sidebar className={open?" show-nav":""}>
          <div className="toggle" tabIndex={-1}  role = "button"  onClick={this.openStateToggler}  onKeyDown = { this.openStateToggler } >
            <span className="toggler">{this.drawMenuIcon()}</span>
          </div>
      <div className="toggle-reverse" tabIndex={-2}  role = "button"  onClick={this.openStateToggler}  onKeyDown = { this.openStateToggler } >
        <span className="toggler">{this.drawMenuIcon()}</span>
      </div>
      <div className="nav-content-wrapper">
          <div className="logo">
            <a href="/">Cookie blog</a>
          </div>
          <ul>
            <li><a href="/">Главная</a></li>
            <li><a href="/about">Об авторе</a></li>
          </ul>
          </div>
        </Sidebar>

    </React.Fragment>
  }
}

export default SidebarMenu